
.banner {
    color: white;
     object-fit: contain;
     height: 448px;
 }
 .banner_contents {
    margin-left: 30px;
     padding-top: 200px;
     height: 190px;
 }
 .banner_title {
     font-size: 3rem;
     font-weight: 800;
     padding-top: 0.3rem ;
 }
 .banner__description  {
     width:45rem;
     line-height: 1;
     padding-top: 1rem;
     font-size: 1.2rem;
     max-width: 360px;
     height: 80px; 
 }
 .banner_button {
   cursor: pointer;
     color: #fff;
     outline: none;
     border: none;
     font-weight: 700;
     border-radius: 0.3vw;
     padding-left: 2rem;
     padding-right: 2rem;
     margin-right: 1rem;
     padding-top: 0.5rem;
     padding-bottom: 0.5rem;
     background-color: rgba(51, 51, 51, 0.5) 
 }
 .play {
   color: #000;
   background-color: #fff;
 }
 .banner_button:hover {
   color: #000;
   background-color: #e6e6e6;
   transition: all 0.2s;
 }
 .banner_fadeBottom {
     height: 30em;
     background-image: linear-gradient(
         180deg,
         transparent,
         rgba(37,37,37,0.91),
         #111
   );
 }
 